import React, { useEffect, useState } from "react";
import posed from "react-pose";
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption,
} from 'reactstrap';

const El = posed.div({
  enter: {
    y: 0,
    opacity: 1
  },
  exit: {
    y: -50,
    opacity: 0
  }
});

const Container = posed.div({
  enter: {
    staggerChildren: 50
  }
});

export default function Portfolios(props) {
  const [certificates, setCertificates] = useState([])

  const [activeIndex, setActiveIndex] = useState(0);
  const [animating, setAnimating] = useState(false);

  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === certificates.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  };

  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? certificates.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  };

  const goToIndex = (newIndex) => {
    if (animating) return;
    setActiveIndex(newIndex);
  };

  const slides = certificates.map((item, key) => {
    return (
      <CarouselItem
        onExiting={() => setAnimating(true)}
        onExited={() => setAnimating(false)}
        key={key}
      >
        <img src={item} alt={item} className="w-100" />
        <CarouselCaption
        // captionText={item}
        // captionHeader={item}
        />
      </CarouselItem>
    );
  });


  function importAll(r) {
    return r.keys().map(r);
  }

  useEffect(() => {
    const imgs = importAll(require.context('../assets/images/certificates', false, /\.(png|jpe?g|svg)$/));
    setCertificates(imgs)

  }, [])

  return (
    <Container>
      <div id="services">
        <El>
          <div className="header">
            <h1>{props.data.lang.menus[5]}</h1>
          </div>
        </El>
        <El>
          <div className="content">
            <Carousel
              activeIndex={activeIndex}
              next={next}
              previous={previous}
              {...props}
            >
              <CarouselIndicators
                items={certificates}
                activeIndex={activeIndex}
                onClickHandler={goToIndex}
              />
              {slides}
              <CarouselControl
                direction="prev"
                directionText="Previous"
                onClickHandler={previous}
              />
              <CarouselControl
                direction="next"
                directionText="Next"
                onClickHandler={next}
              />
            </Carousel>
          </div>
        </El>
      </div>
    </Container>
  )
}